.social {
	padding: 2em auto;
	text-align: center;
	align-content: center;
}

.social a {
	font-size: 1.5rem;
	text-align: center;
	margin: auto 10px;
	align-content: center;
	align-self: center;
	text-decoration: none !important;
}

.social a .fa-facebook-f {
	color: #4267b2;
}
.social a .fa-linkedin-in {
	color: #0077b5;
}

.social a .fa-twitter {
	color: #38a1f3;
}
.social a .fa-instagram {
	color: #bc2a8d;
}
.social a svg:hover {
	color: var(--green);
}

.social a .fa-github {
	color: black;
}
@-webkit-keyframes appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-webkit-keyframes slideIn {
	0% {
		-webkit-transform: translateX(-2%);
		        transform: translateX(-2%);
	}
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@keyframes slideIn {
	0% {
		-webkit-transform: translateX(-2%);
		        transform: translateX(-2%);
	}
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@-webkit-keyframes shrink {
	0% {
		width: 95%;
	}
	100% {
		width: 90%;
	}
}

@keyframes shrink {
	0% {
		width: 95%;
	}
	100% {
		width: 90%;
	}
}

.border-bottom {
	background-color: #333;
}
.home {
	align-content: center;
	justify-content: center;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 5px 1fr 0.2fr 0.5fr;
	grid-template-areas: ".... ...." "title ..." "hire-me hire-me" "social-icons social-icons";
}
.center {
	margin: auto;
	text-align: center;
}
.title {
	justify-content: center;
	width: 100vw;
